import { create } from 'zustand';
import type { StoreMessage } from '@estelle/core';

/**
 * Share  
 *
 *       .
 */
export interface ShareState {
  /**  ID (URL ) */
  shareId: string | null;

  /**    */
  conversationId: number | null;

  /**    */
  messages: StoreMessage[];

  /** WebSocket   */
  isConnected: boolean;

  /**    */
  isAuthenticated: boolean;

  /**   */
  error: string | null;

  // Actions
  setShareId: (shareId: string) => void;
  setConversationId: (conversationId: number | null) => void;
  setConnected: (connected: boolean) => void;
  setAuthenticated: (authenticated: boolean) => void;
  setError: (error: string | null) => void;
  addMessage: (message: StoreMessage) => void;
  setMessages: (messages: StoreMessage[]) => void;
  reset: () => void;
}

/**
 *  
 */
const initialState = {
  shareId: null as string | null,
  conversationId: null as number | null,
  messages: [] as StoreMessage[],
  isConnected: false,
  isAuthenticated: false,
  error: null as string | null,
};

/**
 * Share   
 *
 *       .
 * - shareId:   ID
 * - conversationId:  
 * - messages:   
 * - / 
 * -  
 */
export const useShareStore = create<ShareState>((set) => ({
  ...initialState,

  setShareId: (shareId) => {
    set({ shareId });
  },

  setConversationId: (conversationId) => {
    set({ conversationId });
  },

  setConnected: (connected) => {
    if (connected) {
      set({ isConnected: true });
    } else {
      //      
      set({
        isConnected: false,
        isAuthenticated: false,
      });
    }
  },

  setAuthenticated: (authenticated) => {
    set({ isAuthenticated: authenticated });
  },

  setError: (error) => {
    set({ error });
  },

  addMessage: (message) => {
    set((state) => ({
      messages: [...state.messages, message],
    }));
  },

  setMessages: (messages) => {
    set({ messages });
  },

  reset: () => {
    set({ ...initialState });
  },
}));
