/**
 *    (Vite )
 */
const isDev = import.meta.env?.DEV ?? false;

/**
 * Relay URL  
 * - localhost  ws://localhost:3000 (dev)
 * -    wss://{host} (stage/release  )
 */
function deriveRelayUrl(): string {
  const host = typeof window !== 'undefined' ? window.location.hostname : '';
  if (host === 'localhost' || host === '127.0.0.1') {
    return 'ws://localhost:3000';
  }
  return `wss://${typeof window !== 'undefined' ? window.location.host : 'estelle-relay-v2.fly.dev'}`;
}

/**
 * Relay  
 */
export const RelayConfig = {
  /** Relay  URL */
  url: deriveRelayUrl(),

  /**    */
  maxReconnectAttempts: 5,

  /**   (ms) */
  reconnectInterval: 3000,

  /**   (ms) */
  heartbeatInterval: 30000,

  /**   (ms) */
  connectionTimeout: 10000,
} as const;

/**
 *   
 */
export const ImageCacheConfig = {
  /**    (bytes) -  50MB */
  maxSize: 50 * 1024 * 1024,

  /**    (ms) -  1 */
  expireTime: 60 * 60 * 1000,
} as const;

