import { create } from 'zustand';
import { persist } from 'zustand/middleware';

/**
 *   
 */
export interface UserInfo {
  email: string;
  name: string;
  picture: string | null;
}

/**
 *    
 */
export interface LoginCredential {
  idToken: string;
  user: UserInfo;
}

/**
 *   
 */
export interface AuthState {
  /**    */
  isAuthenticated: boolean;

  /** Google ID  */
  idToken: string | null;

  /**   */
  user: UserInfo | null;

  /**   */
  error: string | null;

  /**   */
  isLoading: boolean;

  // Actions
  login: (credential: LoginCredential) => void;
  logout: () => void;
  getIdToken: () => string | null;
  reset: () => void;
}

/**
 *  
 */
const initialState = {
  isAuthenticated: false,
  idToken: null,
  user: null,
  error: null,
  isLoading: false,
};

/**
 *    
 *
 * Google OAuth     .
 * localStorage    .
 */
export const useAuthStore = create<AuthState>()(
  persist(
    (set, get) => ({
      ...initialState,

      login: (credential: LoginCredential) => {
        set({
          isAuthenticated: true,
          idToken: credential.idToken,
          user: credential.user,
          error: null,
          isLoading: false,
        });
      },

      logout: () => {
        set({
          isAuthenticated: false,
          idToken: null,
          user: null,
          error: null,
          isLoading: false,
        });
      },

      getIdToken: () => {
        return get().idToken;
      },

      reset: () => {
        set({ ...initialState });
      },
    }),
    {
      name: 'estelle-auth', // localStorage  
      partialize: (state) => ({
        //    (error, isLoading )
        isAuthenticated: state.isAuthenticated,
        idToken: state.idToken,
        user: state.user,
      }),
    }
  )
);
